import type { SharedCacheKey } from './cache.service.js';

const SHARED_NAMESPACE = 'shared';

export const SharedCacheKeys = {
    workRecordsHash: {
        rootNamespace: SHARED_NAMESPACE,
        keyName: 'work-records-hash',
    } as SharedCacheKey,
    craftspeopleRecordsHash: {
        rootNamespace: SHARED_NAMESPACE,
        keyName: 'craftspeople-records-hash',
    } as SharedCacheKey,
    partyRecordsHash: {
        rootNamespace: SHARED_NAMESPACE,
        keyName: 'party-records-hash',
    } as SharedCacheKey,
    onlineUserSockets: (ccId: string) => {
        return {
            rootNamespace: SHARED_NAMESPACE,
            keyName: 'online-users',
            identifier: ccId,
        } as SharedCacheKey;
    },
};
